@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap);
.header{transition:all 0.2s var(--ease-in-out)}.header__brand{color:var(--color-white);font-weight:bold}.header__menu{width:34px;height:34px;fill:var(--color-white)}.header__menu:hover{background-color:var(--color-primary-light)}.header__icon{height:34px;width:34px;fill:var(--color-white)}.header__nav ul{list-style:none;padding:0;margin:0}.header__nav ul li:hover{background-color:var(--color-primary-light)}.header__nav ul .active-link{background-color:var(--color-primary-light)}.header__nav ul .apply-padding-y{padding-top:var(--space-md);padding-bottom:var(--space-md)}.header__nav-link{display:block;width:100%;text-align:start;color:var(--color-white)}

.patients-page{display:grid;grid-template-rows:-webkit-min-content 1fr;grid-template-rows:min-content 1fr}.patients-page .container{height:100%;overflow-y:scroll;-ms-overflow-style:none;scrollbar-width:none;display:grid;grid-template-columns:2fr 4fr;grid-gap:var(--space-md);gap:var(--space-md)}.patients-page .container::-webkit-scrollbar{display:none}.patients-page .patient-row{display:grid;grid-template-columns:repeat(3, 1fr);align-items:center}.patients-page .menu-container{position:relative}.patients-page .menu-container .more-icon{width:30px;height:30px}.patients-page .menu-container .dropdown-menu{position:absolute;z-index:var(--z-index-popover);right:10px;top:100%;background-color:var(--color-bg-light);color:var(--color-text);box-shadow:var(--shadow-md)}.patients-page .menu-container .dropdown-menu ul li{padding:var(--space-sm);padding-bottom:var(--space-xs)}.patients-page .menu-container .dropdown-menu ul li:hover{background-color:var(--color-bg-dark)}


.overlay{position:fixed;z-index:var(--z-index-fixed-element);top:0;bottom:0;left:0;right:0}.dialog-box{position:fixed;z-index:var(--z-index-overlay);top:10vh;bottom:10vh;left:20vw;right:20vw}.dialog-box .close-container{position:absolute;right:var(--space-md);top:var(--space-sm)}

.test-exams-list .search-container{width:70%}.test-exams-list ul li:hover{background-color:var(--color-contrast-lower)}

.search-box{position:relative;display:flex;align-items:center}.search-box__search-icon{position:absolute;left:1rem;fill:var(--color-contrast-low)}.search-box input{display:block;width:60%;transition:width 0.2s var(--ease-in-out);padding-left:2.5rem}.search-box input:focus{width:100%}

.patient-form{display:grid;grid-template-rows:-webkit-min-content 1fr;grid-template-rows:min-content 1fr;grid-gap:var(--space-md)}.patient-form .form-container{display:grid;grid-template-columns:1fr 1fr;grid-gap:var(--space-md);gap:var(--space-md)}.patient-form .form-grid{display:grid;grid-template-columns:repeat(2, 1fr);grid-gap:var(--space-sm);gap:var(--space-sm)}.patient-form .test-exam-item{display:grid;grid-template-columns:repeat(2, 1fr);grid-gap:var(--space-md);gap:var(--space-md)}

.new-patient-page{height:100%}.new-patient-page .container{height:100%;display:grid;grid-template-columns:repeat(6, 1fr);grid-template-areas:"left left left left right right";grid-gap:var(--space-md);gap:var(--space-md)}.new-patient-page .container .left{grid-area:left}.new-patient-page .container .right{grid-area:right}

.print-page{width:210mm;min-height:297mm;margin:0 auto;border:1px solid var(--gray-2)}@media print{.header{display:none}.app,main,section,.print-page{width:100% !important;border:none !important;padding:0 !important;margin:0;color:var(--color-black)}body{font-family:Georgia, "Times New Roman", Times, serif;font-size:16pt;line-height:24pt;background:none;color:var(--color-black)}h1,h2,h3,h4,h5,h6{color:var(--color-black) !important;font-size:24pt;line-height:30pt;margin:0}h1{font-size:50pt;margin:0 0 20pt;text-align:center;width:100%;-webkit-transform:rotate(0);transform:rotate(0)}h1 small{font-size:24pt;color:#888;margin:0 0 10pt}p{color:var(--color-black) !important}}

.circle{border:2px solid var(--color-black);box-shadow:0 -0.5rem 0 0 rgba(0,0,0,0.2);border-radius:50%}@media print{.circle{width:100vw !important}}

.print-patient-info{display:grid;grid-template-columns:repeat(2, 1fr);grid-gap:var(--space-xl)}


.grid-result{display:grid;grid-template-columns:2fr 1fr}

